* {
  box-sizing: border-box;
  transition: .2s;
  font-weight: 400; }

html {
  font-family: "lato", Helvetica;
  line-height: 1.5;
  color: #DADADA; }

body {
  margin: 0;
  background-color: #030C1C; }

ul {
  margin: 0;
  padding: 0; }

input {
  border: none; }
  input:focus {
    outline: none; }

button:focus, button:active {
  outline: none; }

.heading_main {
  font-size: 3.125rem;
  font-weight: lighter; }

.heading_section {
  opacity: 0;
  visibility: hidden; }

.link {
  text-decoration: none; }
  .link_primary {
    color: #DADADA; }

.navigation {
  position: fixed;
  top: 0;
  height: 70px;
  width: 100%;
  z-index: 2; }

.navigation-list {
  display: flex;
  flex-direction: row;
  justify-content: center; }
  .navigation-list__item_inline {
    display: inline-block;
    transition: border-bottom .2s ease;
